@charset "utf-8";
@import "variables", "themes";

/* ================= my custom styling================== */

html, body {
 color: var(--text-color);
 background-color: var(--bg-color);
 font-size: 16px;
 line-height: 1.5;
 height: 100%;
 display: flex;
 flex-direction: column;
}

/*#main-container, #navbar {
 max-width: 1000px;
 margin: auto;
}*/

a, a:hover {
 color: var(--theme-color);
}

table {
 th, td {
 font-size: 1em;
 padding-left: 0;
 padding-right: 1em;
 padding-top: .5em;
 padding-bottom: .5em;
 vertical-align: top;
 }
}

table.chrono {
 tr td:first-child {
 white-space: nowrap;
 }
}

#profile-img {
 border-radius: 50%;
 padding: 3.5px;
 border: 1px solid var(--divider-color);
 max-width: 170px;
 opacity: .95;
}

.navbar-brand {
 font-size: 1.12em;
}

.affiliation {
 a > span, a > i { 
 color: var(--text-color); 
 }
 border-bottom: 1px solid var(--divider-color);
}

.notice {
 background: var(--code-bg-color);
 border: 1px solid var(--divider-color);
}

h1, h2 {
 opacity: .98;
 font-weight: 500;//bold;
}

h1 {
 font-size: 1.95em;//2.25em;
}

h2 {
 font-size: 1.7em;
}

.mt-minus4 {
 margin-top: -4em;
}

ol.bibliography {
 margin: 0;
 padding: 1.5em;
 list-style: inherit;
}

ol.bibliography.mt-minus1 {
 margin-top: -.75em;
}

.bib-links {
 line-height: 2.2em;
}

.bib-link {
 white-space: nowrap;
 margin-right: .3em;
 padding: 4px 5.5px;
 border-radius: 3px;
 //border: 1px solid var(--text-color);
 border: 1px solid $grey-color;
 color: var(--text-color);
 &:hover {
 text-decoration: none;
 background: var(--theme-color);
 border: 1px solid var(--theme-color);
 color: var(--bg-color);
 }
}

footer {
 //background: var(--code-bg-color);
 margin-top: auto;
 border-top: 1px solid var(--divider-color);
}


/* responsive design */
@media (max-width: 576px) {
 #profile-img {
 max-width: 7em;
 }
 .affiliation {
 border-bottom: none;
}
 .social {
 font-size: 1.5em;
 }

}