/*******************************************************************************
 * Variables used throughout the theme.
 * To adjust anything, simply edit the variables below and rebuild the theme.
 ******************************************************************************/


// Colors
$red-color: #FF3636 !default;
$red-dark-color: #B71C1C !default;

$orange-color: #F29105 !default;

$blue-color: #0076df !default;
$blue-dark-color: #00369f !default;

$cyan-color: #2698BA !default;
$cyan-light-color: lighten($cyan-color, 25%);
$cyan-lighter-color: lighten($cyan-color, 30%);
$cyan-dark-color: darken($cyan-color, 5%);
$cyan-darker-color: darken($cyan-color, 10%);

$green-color: #00ab37 !default;
$green-lime-color: #B7D12A !default;
$green-dark-color: #009f06 !default;
$green-light-color: #ddffdd !default;
$green-bright-color: #11D68B !default;

$purple-color: #B509AC !default;
$purple-light-color: lighten($purple-color, 25%);

$pink-color: #f92080 !default;
$pink-light-color: #ffdddd !default;

$yellow-color: #efcc00 !default;

$grey-color: #828282 !default;
$grey-light-color: lighten($grey-color, 25%);
$grey-lighter-color: lighten($grey-color, 45%);
$grey-dark-color: #1C1C1D;

$white-color: #ffffff !default;
$black-color: #000000 !default;
